import Head from '~/components/Head'
import { Button, List, InputItem, Toast, WingBlank, WhiteSpace } from 'antd-mobile'
import { failToast, loadingToast, successToast } from '&/methods'
import { axios } from '&/axios'

export default class AddUser extends Component {
  constructor() {
    super();
    this.state = {
      username: '',
      usertel: ''
    }
  }


  savaUser = () => {
    var usertel = this.usertel.state.value;
    var username = this.username.state.value;
    console.log(username, usertel);
    if (username.length == 0) {
      failToast('请输入账户', 0.8);
      return
    }
    if (usertel.length == 0) {
      failToast('请输入手机号码', 0.8);
      return
    }
    axios.post('/bookstore/userAdd', { usertel, username }).then(res => {
      if(res.data.type==0){
        failToast('手机号已存在',0.8)
      }
      this.setState({
        username: '',
        usertel: ''
      })
    })

  }

  render() {
    return (
      <div style={{ height: '100vh', backgroundColor: '#fff' }}>
        <Head title='用户新增' showBack={true}></Head>
        <div>
          <List>
            <InputItem
              clear
              placeholder="请输入账户"
              ref={el => this.username = el}
              onChange={v=>{this.setState({username:v})}}
              value={this.state.username}
            >账户姓名</InputItem>
            <InputItem
              clear
              placeholder="请输入电话号码"
              ref={el => this.usertel = el}
              onChange={v=>{this.setState({usertel:v})}}
              value={this.state.usertel}
            >电话号码</InputItem>
          </List>
        </div>
        <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
          <Button type='primary' style={{ width: '95%' }} onClick={this.savaUser}>保存</Button>
        </div>
      </div>
    )
  }
}